<template>
    <div v-show="bottle.show">
        <div class="area" :style="{ left: left, top: top }">
            <div class="bottle">
                <div
                    v-for="(wine, index) in bottle.wines"
                    :key="index"
                    :style="{ backgroundColor: wine }"
                    class="wine"
                ></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "FloatBottle",
    props: ["bottle"],
    data() {
        return {
            left: 0,
            top: 0,
        };
    },
    mounted() {
        document.addEventListener("mousemove", this.set_pos);
    },
    destroyed() {
        document.removeEventListener("mousemove", this.set_pos);
    },
    methods: {
        set_pos(event) {
            this.left = event.clientX + 10 + "px";
            this.top = event.clientY + 10 + "px";
        },
    },
};
</script>

<style scoped>
.area {
    position: fixed;
    height: 200px;
    width: 80px;
}

.bottle {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
    border: 1px solid black;
}

.wine {
    height: 22%;
    width: 100%;
}
</style>