<template>
    <div class="main" @click="put">
        <FloatBottle :bottle="bottleFloat" />
        <div class="game-area">
            <div class="container">
                <Bottle
                    v-for="(bottle, index) in bottleArr"
                    :key="index"
                    :bottle_click="bottle_click"
                    :bottle="bottle"
                />
            </div>
            <div>
                <img alt="Vue logo" src="@/assets/logo.png" />

                <p>当前倒水次数{{ cnt }}</p>
                <button @click.stop="restart">点我重玩</button>

                <h1>{{ msg }}</h1>
            </div>
        </div>
    </div>
</template>

<script>
import Bottle from "@/components/Bottle";
import FloatBottle from "@/components/FloatBottle";
import { nanoid } from "nanoid";
export default {
    name: "BottleGame",
    components: { Bottle, FloatBottle },

    data() {
        return {
            msg: "",
            cnt: 0,
            bottleArr: [],
            bottleFloat: {
                id: "",
                show: false,
                wines: [],
            },
        };
    },

    created() {
        for (let i = 0; i < 14; i++) {
            let bottle = {
                id: nanoid(),
                show: true,
                wines: [],
            };
            this.bottleArr.push(bottle);
        }
        this.load();
    },

    methods: {
        load() {
            let cs = [
                "blue",
                "gray",
                "red",
                "purple",
                "pink",
                "olive",
                "green",
                "orange",
                "yellow",
                "lightblue",
                "navy",
                "greenyellow",
            ];
            let cs_temp = [];
            cs.forEach((c) => {
                for (let i = 0; i < 4; i++) cs_temp.push(c);
            });
            console.log(cs_temp);
            this.shuffleSelf(cs_temp);
            this.bottleArr.forEach((b, i) => {
                b.wines = [];
                if (i < 12)
                    for (let j = 0; j < 4; j++)
                        b.wines.push(cs_temp[i * 4 + j]);
            });
        },
        shuffleSelf(array) {
            var index = -1,
                length = array.length;
            while (++index < length) {
                // var rand = baseRandom(index, lastIndex),
                var rand = index + Math.floor(Math.random() * (length - index));
                var value = array[rand];

                array[rand] = array[index];

                array[index] = value;
            }
            return array;
        },
        bottle_click(id) {
            console.log(id);
            this.bottleArr.some((b) => {
                if (b.id == id) {
                    if (b.show) {
                        if (this.bottleFloat.show) {
                            // 倒入
                            if (
                                b.wines.length < 4 &&
                                this.bottleFloat.wines.length > 0
                            ) {
                                console.log("pour");
                                b.wines.unshift(this.bottleFloat.wines.shift());
                                this.cnt++;
                            }
                        } else {
                            // 拿起
                            console.log("pick");
                            this.bottleFloat = {
                                id: b.id,
                                show: true,
                                wines: b.wines.slice(),
                            };
                            b.show = false;
                        }
                    } else {
                        // 放下
                        console.log("put");
                        this.bottleFloat.show = false;
                        b.show = true;
                        b.wines = this.bottleFloat.wines;
                    }
                    return true;
                }
            });
            if (this.isWin()) this.msg = "恭喜通关（乐）";
        },
        isWin() {
            // 检测是否胜利
            let flag = 1;
            this.bottleArr.some((b) => {
                if (b.wines.length != 0 && b.wines.length != 4) {
                    flag = 0;
                    return true;
                }
                if (b.wines) {
                    let s = b.wines[0];
                    b.wines.forEach((e) => {
                        if (e != s) {
                            flag = 0;
                        }
                    });
                    if (!flag) return true;
                }
            });
            return flag;
        },
        restart() {
            this.put();
            this.cnt = 0;
            this.msg = "";
            this.load();
        },
        put() {
            if (this.bottleFloat.show) {
                // 放下
                console.log("put");
                this.bottleFloat.show = false;
                this.bottleArr.forEach((b) => {
                    if (b.id == this.bottleFloat.id) {
                        b.show = true;
                        b.wines = this.bottleFloat.wines;
                    }
                });
            }
        },
    },
};
</script>

<style scoped>
img {
    width: 60px;
    height: 60px;
}
button {
    height: 60px;
    width: 60px;
    font-size: 20px;
}
.main {
    width: 100%;
    height: 600px;
    padding-top: 60px;
}
.game-area {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}
.container {
    display: grid;
    grid-template-rows: repeat(2, 200px);
    grid-template-columns: repeat(7, 80px);
    gap: 10px;
    justify-content: center;
}
</style>